<template>
    <transition name='slide-fade'>
    <div class="address">
        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="position:absolute;width:0;height:0">
            <defs>
                <symbol viewBox="0 0 8 13" id="arrow-right"><path fill="#D1D1D6" fill-rule="evenodd" d="M1.784 12.714l5.91-5.524a.932.932 0 0 0 0-1.38L1.784.286a1.097 1.097 0 0 0-1.478 0 .932.932 0 0 0 0 1.38L5.478 6.5.306 11.333a.932.932 0 0 0 0 1.381c.408.381 1.07.381 1.478 0z"></path></symbol>
                <symbol viewBox="0 0 18 18" id="edit"><g fill="#999" fill-rule="evenodd"><path d="M8.513 3v1H2.076A.076.076 0 0 0 2 4.075v11.85c0 .041.034.075.075.075h11.85a.074.074 0 0 0 .075-.072V9.521h1v6.407C15 16.52 14.518 17 13.925 17H2.075A1.076 1.076 0 0 1 1 15.925V4.075C1 3.481 1.481 3 2.076 3h6.437z"></path><path fill-rule="nonzero" d="M7.1 10.04l.068 1.312 1.306.067 7.648-7.59-1.42-1.404L7.1 10.04zm7.95-8.676l2.134 2.109a.5.5 0 0 1 .004.707l-8.324 8.26-2.648-.137-.138-2.655 8.267-8.282a.5.5 0 0 1 .705-.002z"></path></g></symbol>
                <symbol xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 16 16" id="delete"><defs><path id="delete_a" d="M13.132 12l6.47-6.47a.75.75 0 0 0-1.061-1.06l-6.47 6.47-6.47-6.47a.75.75 0 0 0-1.06 1.06L11.01 12l-.072.071.071.071-6.47 6.47a.75.75 0 0 0 1.061 1.06l6.47-6.47 6.47 6.47a.75.75 0 0 0 1.06-1.06l-6.47-6.47.072-.07-.071-.072z"></path></defs><use fill="#999" fill-rule="nonzero" transform="translate(-4 -4)" xlink:href="#delete_a"></use></symbol>
            </defs>
        </svg>
        <Header :title="title" />
        <template v-if="$route.hash.indexOf('#/add') == -1 && $route.hash.indexOf('#/edit') == -1">
            <section>
                <ul>
                    <li v-for="(item , index) in addressList" :key="item.id">
                        <div class="left">
                            <p class="address-title">
                                <strong>{{item.username}}</strong>
                                <span>{{item.gender}}</span>
                                {{item.tel}}
                            </p>
                            <p class="address-detail">{{item.address}}</p>
                        </div>
                        <div class="right">
                            <svg class="eidt-icon" @click="edit(item)"><use xlink:href="#edit"></use></svg>
                            <svg class="del-icon" @click="del(index)"><use xlink:href="#delete"></use></svg>
                        </div>
                    </li>
                </ul>
            </section>
            <div class="add-btn" @click="add">
                <img src="data:image/png;base64," class="bottom-icon">
                <div></div>
            </div>
        </template>
        <template v-else>
            <div class="address-modify">
                <div class="form-item">
                    <strong></strong>
                    <div class="item-inner">
                        <input type="text" v-model="address.username" placeholder="">
                        <div class="gender">
                            <span class="radio activted"></span>
                            <span class="radio"></span>
                        </div>
                    </div>
                </div>
                <div class="form-item">
                    <strong></strong>
                    <div class="item-inner">
                        <input type="tel" v-model="address.tel" placeholder="">
                    </div>
                </div>
                <div class="form-item">
                    <strong></strong>
                    <div class="item-inner">
                        <input type="address" v-model="address.address" placeholder="//" readonly>
                        <svg><use xlink:href="#arrow-right"></use></svg>
                    </div>
                </div>
                <div class="form-item detail">
                    <strong></strong>
                    <div class="item-inner">
                        <textarea type="text" v-model="address.detail" placeholder="105501"></textarea>
                        <svg class="edit-icon" @click="edit(item)"><use xlink:href="#edit"></use></svg>
                    </div>
                </div>
                <div class="form-item">
                    <strong></strong>
                    <div class="item-inner">
                        <span class="radio"></span>
                        <span class="radio"></span>
                        <span class="radio"></span>
                    </div>
                </div>
            </div>
            <button></button>
        </template>
    </div>
    </transition>
</template>

<script>
import { MessageBox } from 'mint-ui';
export default {
    name: 'Address',
    components: {
        Header : () => import('@/components/Header')
    },
    data () {
        return {
            title:'',
            address:{
                username:'',
                gender:'',
                tel:'',
                address:'',
                detail:''
            },
            addressList:[
                {id:1,username:'',gender:'',tel:'15656892160',address:''},
                {id:2,username:'',gender:'',tel:'18344582474',address:'()53 4401'}
            ]
        }
    },
    beforeRouteUpdate (to , from , next) {
        this.title = to.hash == '#/add' ? '' : to.hash == '#/edit' ? '': ''
        next ();
    },
    methods : { 
        add () {
            this.$router.push('/profile/address/#/add')
            this.address.username = '';
            this.address.gender = '';
            this.address.tel = '';
            this.address.address = '';
        },
        edit (address) {
            this.$router.push('/profile/address/#/edit');
            this.address = {...address} ;
        },
        del (index) {
            MessageBox.confirm('' , '').then(action => {
                console.log(action);
                this.addressList.splice(index , 1);
            }).catch( () => {
                
            });
        }
    }
}
</script>

<style scoped lang="scss">
.flex {
  display:-webkit-box; 
  display:-webkit-flex;
  display:flex;
}
.flex-center {
  -webkit-box-align:center;
  -webkit-align-items:center;
  align-items:center;
}
.flex-space-between {
  -webkit-box-pack:justify;
  -webkit-justify-content:space-between;
  justify-content:space-between;
}
.flex-content-center {
	-webkit-box-pack:center;
  	-webkit-justify-content:center;
  	justify-content:center;
}
.address {
    height: 100%;
    svg {
        width:12px;
        height:12px;
    }
    section {
        ul {
            background-color:#fff;
            margin-bottom:34px;
            li {
                @extend .flex;
                @extend .flex-center;
                padding:10px;
                border-bottom:1px solid #ddd;/*no*/
                .left {
                    -webkit-box-flex: 1;
                    flex: 1;
                    font-size:12px;
                    color:#666;
                    margin-right:5px;
                    strong {
                        color:#000;
                    }
                    p.address-detail {
                        margin-top:3px;
                        font-size:10px;
                    }
                }
                .right {
                    width: 40px;
                    text-align: center;
                    svg.del-icon {
                        width: 10px;
                        height: 10px;
                        margin-left:5px;
                    }
                }
            }
        }
    }
    .add-btn {
        @extend .flex;
        @extend .flex-center;
        @extend .flex-content-center;
        height:34px;
        position: fixed;
        width: 100%;
        bottom: 0;
        left: 0;
        background-color: #fff;
        color:#3190e8;
        border-top: 1px solid #ddd;/*no*/
        img {
            width:16px;
            height:16px;
            margin-right:5px;
        }
    }
}
.address-modify {
    background-color: #fff;
    padding:0 10px;
    .form-item {
        @extend .flex;
        @extend .flex-center;
        padding:10px 0;
        strong {
            font-size:10px;
            min-width:40px;
        }
        input , textarea {
            width: 100%;
            margin-right:10px;
            color: #666;
            border: none;
            outline: none;
        }
        .radio {
            min-width: 40px;
            text-align: center;
            padding: 3px 0;
            display: inline-block;
            border: 1px solid #ddd;
            border-radius: 2px;
            margin-right: 10px;
            color: #666;
        }
        .radio.activted {
            color: #2395ff;
            background-color: #eef7ff;
            border-color: rgba(35,149,255,.18);
        }
        .item-inner {
            @extend .flex;
            @extend .flex-center;
            -webkit-box-flex: 1;
            flex: 1;
            svg.edit-icon {
                width:16px;
                height:16px;
            }
            .gender {
                flex: none;
            }
        }
    }
    .detail {
        -webkit-box-align:flex-start;
        -webkit-align-items:flex-start;
        align-items:flex-start;
        .item-inner {
            -webkit-box-align:flex-start;
            -webkit-align-items:flex-start;
            align-items:flex-start;
        }
    }
}
button {
    width: 90%;
    margin: 0 auto;
    display: block;
    border-radius: 4px;
    background-color: #00d762;
    color:#fff;
    height:32px;
    margin-top: 20px;
}
</style>
      


