			</div>
			<div class="profile-info">
				<div @click="isLogin ? setUserInfo() : toLogin()">
					<p>{{profileInfo.user.name}}</p>
					<p class="mobile">
						<svg fill="#fff">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#mobile"></use>
						</svg>
						<span>{{profileInfo.user.mobile}}</span>
					</p>
				</div>
			</div>
			<div class="profile-arrow">
				<svg fill="#fff">
					<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
				</svg>
			</div>
		</div>
		<div class="maininfo">
			<div class="wallet">
				<p>
					<span>{{profileInfo.user.wallet}}</span>
					<em></em>
				</p>
				<p></p>
			</div>
			<div class="red-bag">
				<p>
					<span>{{profileInfo.user.redbag}}</span>
					<em></em>
				</p>
				<p></p>
			</div>
			<div class="gold-coin">
				<p>
					<span>{{profileInfo.user.goldcoin}}</span>
					<em></em>
				</p>
				<p></p>
			</div>
		</div>
		<router-link class="link-item" v-for="item in profileInfo.list" :to="item.url" :key="item.id" :style="{marginTop:item.marginTop}">
			<div class="profile-item">
				<div class="item-icon">
					<svg fill="#4aa5f0">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="item.icon"></use>
					</svg>
				</div>
				<div class="item-name">
					<span>{{item.text}}</span>
					<svg fill="#fff">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
					</svg>
				</div>
			</div>
		</router-link>
		<FooterMenu/>
	</div>
</template>
<script>
import { parseImage } from '@/utils/Function'
export default {
	name: 'Profile',
	components: {
		Header: () => import('@/components/Header'),
		FooterMenu: () => import('@/components/FooterMenu')
	},
	data() {
		return {
			isLogin:false,
			profileInfo: {
				user: {
					image_hash: '', // cc1d25b70a0db235c45115e04ba08c05jpeg
					name: '/',
					mobile: '', // 183****2474
					wallet: '0.00',
					redbag: 0, // 5
					goldcoin:0 // 77
				},
				list: [
					{ id: 1, text: '', icon: '#address', marginTop: '10px', url: '/profile/address' },
					{ id: 2, text: '', icon: '#point', marginTop: '10px', url: '/' },
					{ id: 3, text: '10', icon: '#commend', url: '/' },
					{ id: 4, text: '', icon: '#service', marginTop: '10px', url: '/' },
					{ id: 5, text: 'APP', icon: '#download', url: '/' }
				]
			}
		}
	},
	created () {
		if(this.$store.state.token) {
			this.isLogin = true ;
			this.loadData()
		}
	},
	methods: {
		loadData () {
			this.$http.get('/ele/profile').then(res => {
				this.profileInfo.user = res.data;
			}).catch( () => {

			})
		},
		setUserInfo () {
			
		},
		toLogin () {
			this.$router.push('/login');
		},
		getImage(image_hash , imageMogr) {
			if(image_hash == '') {
				return 'data:image/png;base64,';
			}
			return parseImage(image_hash, imageMogr)
		}
	}
}
</script>

<style scoped lang="scss">
.profile {
	.profile-content {
		display: flex;
		padding: 20px 15px;
		background-image: linear-gradient(90deg, #0af, #0085ff);
		align-items: center;
		.profile-icon {
			width: 54px;
			height: 54px;
			img {
				width: 100%;
				height: 100%;
				border-radius: 50%;
			}
		}
		.profile-info {
			-webkit-flex-grow: 1;
			flex-grow: 1;
			margin-left: 10px;
			font-size: 18px;
			font-weight: 700;
			font-family: PingFang SC, Helvetica Neue, Arial, sans-serif;
			color: #fff;
			p {
				color:#fff;
				svg {
					width: 8px;
					height: 10px;
				}
			}
			.mobile {
				margin-top: 5px;
				font-size: 10px;
			}
		}
		.profile-arrow {
			svg {
				width: 10px;
				height: 10px;
			}
		}
	}
	.maininfo {
		width: 100%;
		height: 74px;
		display: table;
		background-color: #fff;
		div {
			width: calc(100% / 3);
			display: table-cell;
			text-align: center;
			vertical-align: middle;
			p {
				color: #666;
				font-weight: 700;
				font-size: 11px;
				span {
					font-family: PingFang SC, Helvetica Neue, Arial, sans-serif;
					font-size: 22px;
				}
				em {
					font-style: normal;
				}
			}
		}
		.wallet {
			span {
				color: rgb(0, 152, 251);
			}
			em {
				color: rgb(0, 152, 251);
			}
		}
		.red-bag {
			span {
				color: rgb(255, 95, 62);
			}
			em {
				color: rgb(255, 95, 62);
			}
		}
		.gold-coin {
			span {
				color: rgb(106, 194, 11);
			}
			em {
				color: rgb(106, 194, 11)
			}
		}
	}
	.link-item {
		background-color: #fff;
		padding: 0 10px;
		display: block;
		.profile-item {
			display: flex;
			padding: 10px 0;
			-webkit-align-items: center;
			align-items: center;
			font-size: 14px;
			.item-icon {
				margin-right: 8px;
				svg {
					width: 16px;
					height: 16px;
				}
			}
			.item-name {
				display: flex;
				align-items: center;
				-webkit-justify-content: space-between;
				justify-content: space-between;
				flex: 1;
				svg {
					width: 10px;
					height: 10px;
					fill: #bbb;
				}
			}
		}
	}
}
</style>


