import Vue from 'vue'
import Vuex from 'vuex' 

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		token:null,
		loading:false,
		location:null,
		shoppingCart:[]
	},
	// mutations 
	mutations : {
		loading (state , loading) {
			state.loading = loading ;
		},
		setLocation (state , location) {
			state.location = location;
		},
		setToken (state , token) {
			state.token = token;
		}
	},
	// actions,mutationstate
	//  store.dispatch('testMehtod') actions
	actions : {
		// 
		testMehtod ({commit}) {
			commit('mutationMethod')
		}
	}
})



